import productList from "./productList";
export default {
  data() {
    return {
      tabIndex: 0,
      active: 0,
      currentView: "productList",
      tabs: [
        {
          type: "稳健",
          view: "productList"
        }
      ]
    };
  },
  components: {
    productList
  },
  methods: {
    toggle(i, v) {
      this.active = i;
      this.currentView = v;
    }
  }
};



